.about {
	padding: 0.5em;
}

.about p {
	padding-left: 1.5em;
	padding-right: 1.5em;
}

.about.light h1,
.about.light p,
.about.light h3,
.about.light li {
	color: #000000;
	transition: color ease-in 1s;
}

.about.dark h1,
.about.dark p,
.about.dark h3,
.about.dark li {
	color: #ffffff;
	transition: color ease-in 1s;
}